body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo-wrapper {
    display: none;
}

.logo-title {
    font-family: Righteous !important;
    font-size: 24px;
    line-height: 30px;
}

.checkbox-toggle{
    display: flex;
    align-items: center;
    margin: 0 auto;
    min-height: 36px;
    position: relative;
}

span.choice{
    content: " ";
    width: 49px;
    height: 3px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: #c6c6c6;
    outline: none;
    box-shadow: inset 0 0 5px rgba(0,0,0, .2);
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: relative;
    cursor: pointer;
    margin: 0 14px;
}

.not-eat,
.eat {
    font-family: Roboto;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.01px;
    color: #4D4D4D;
    cursor: pointer;
    margin: 0;
}

.checkbox-circle {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #807E7F;
    top: 8px;
    left: 60px;
    background: #fff;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    z-index: 1;
}

input#choice:checked + .mainWrapper .checkbox-circle {
    left: 105px;
    background: #02a9f4;
    border-color: #02a9f4;
}

@media (min-width: 768px) {
    .logo-wrapper {
        position: absolute;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}
.blockTitle {
    margin: 0 0 20px 0;
    font-size: 24px;
}


@media (min-width: 768px) {
    .menuWrapper {
        -webkit-column-count: 2;
                column-count: 2;
        grid-column-gap: 28px;
        -webkit-column-gap: 28px;
                column-gap: 28px;
    }
}

.menu {
    background: #fafafa;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
    border-radius: 3px;
    padding: 17px 13px;
    margin-bottom: 15px;
}

.menu:disabled {
    opacity: 0.2;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
}

.menu h4 {
    font-size: 18px;
    font-weight: 500;
}

.menu__list {
    list-style: none;
    padding: 0;
    font-size: 14px;
}

.menu__list-item {
    display: flex;
}

.menu__list-item:not(:last-child) {
    margin-bottom: 10px;
}

.menu__list-item * {
    cursor: pointer;
}

.menu__list-item label {
    margin: 0;
}

/*Begin Стилизованный чекбокс */
.menu__checkbox_hidden {
    width: 0;
    height: 0;
    visibility: hidden;
}

.menu__checkbox_hidden:checked + .menu__checkbox {
    background-color: #009688;
    border: none;
}
.menu__checkbox_hidden:checked + .menu__checkbox:after {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 7px;
    width: 6px;
    height: 12px;
    border: solid #dadada;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.menu__checkbox {
    display: flex;
    position: relative;
    height: 19px;
    width: 19px;
    min-width: 19px;
    margin-right: 10px;
    border: 2px solid #dadada;
    border-radius: 2px;
    -webkit-transition: background-color 0.5s, border 0.3s;
    transition: background-color 0.5s, border 0.3s;
}

label.menu__checkbox-label {
    line-height: 1.4;
}
/*End Стилизованный чекбокс */

@media (min-width: 768px) {
    .menu {
        display: block;
        page-break-inside: avoid;
    }
}
.btn-primary {
    font-size: 20px;
    padding-left: 40px;
    padding-right: 40px;
    width: 100%;
}

@media (min-width: 576px) { 
    .btn-primary {
        display: block;
        max-width: 290px;
        width: 290px;
        margin: 0 auto;
    } 
}
.EntryFormWrapper {
    display: flex;
    align-items: center;
    text-align: center;
    background: #f2f2f2;
    height: 100vh;
    overflow-y: auto
}

.form-signin {
    width: 100%;
    max-width: 330px;
    padding: 25px;
    margin: auto;
}

.form-signin .btn {
    margin-bottom: 24px;
    max-width: 140px;
    padding-left: 20px;
    padding-right: 20px;
}

.inputForm {
    text-align: center;
    border: none;
    border-bottom: 2px solid #979797;
    background: transparent;
    outline: none;
    width: 100%;
    font-style: italic;
    font-size: 18px;
    line-height: 21px;
}

::-webkit-input-placeholder {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); 
    -webkit-transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s;
    opacity: 1;
}

::-ms-input-placeholder {
    transform: translateY(0px); 
    -webkit-transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s;
    opacity: 1;
}

::placeholder {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); 
    -webkit-transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s; 
    transition: transform 0.3s, font-size 0.3s, opacity 0.3s, -webkit-transform 0.3s;
    opacity: 1;
}

.inputForm:focus::-webkit-input-placeholder,
.inputForm:active:focus::-webkit-input-placeholder {
  font-size:12px;
  -webkit-transform:translateY(-20px);
          transform:translateY(-20px);
  opacity: 0;
}

.entryFormInfo {
    display: block;
    font-size: 12px;
    line-height: 14px;
    color: #898989;
}
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: Roboto;
}
.App-logo {
  height: 36px;
  width: 36px;
  margin-right: 10px;
}

.App-header {
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  background-color: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.26);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
}

.pageWrapper {
  margin-top: 60px;
  padding: 0 15px;
  visibility: hidden;
}

#choice.choice {
  width: 0;
  height: 0;
}

input#choice:checked + .mainWrapper .pageWrapper {
  visibility: visible;
}

.menuFormWrapper {
  max-width: 800px;
  margin: 0 auto;
}

.menuFormWrapper > div {
  padding-top: 20px;
}
